import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Card, Row, Col, Divider } from 'antd';
import MenuTabTop from './MenuTabTop';
import FormInfoCommonCreateNewClass from './FormInfoCommonCreateNewClass';
import TableCreateNewClass from './TableCreateNewClass';
import './style.scss';

class CreateNewClassPage extends Component {
  state = {};
  render() {
    return (
      <Card title="Danh sách lớp tín chỉ">
        <Row>
          <Col span={24}>
            <FormInfoCommonCreateNewClass />
          </Col>
        </Row>
        <Divider>Danh sách lớp TC</Divider>
        <Row>
          <Col span={24}>
            <TableCreateNewClass />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <CreateNewClassPage />
  </Page>
);



