import React, { Component } from 'react';
import { Row, Col, Menu, Icon } from 'antd';
import './style.scss';

class MenuTabTop extends Component {
  state = {};

  handleClick = e => {
    // console.log('click ', e);
    this.setState({
      current: e.key,
    });
    if (e.key === 'enteringInfo') {
      console.log('render');
    }
  };
  render() {
    return (
      <Row>
        <Col span={24}>
          <Menu onClick={this.handleClick} selectedKeys={[this.state.current]} mode="horizontal">
            <Menu.Item key="summary">
              <Icon type="setting" />
              Tổng hợp số liệu
            </Menu.Item>
            <Menu.Item key="createClass">
              <a href="#/students/statisticsDataForOpenClass/createNewClass">
                <Icon type="file-excel" theme="twoTone" twoToneColor="#00cc00" />
                Tạo lớp tín chỉ
              </a>
            </Menu.Item>
            <Menu.Item key="fileExcel">
              <Icon type="file-excel" theme="twoTone" twoToneColor="#00cc00" />
              Xuất excel
            </Menu.Item>
          </Menu>
        </Col>
      </Row>
    );
  }
}

export default MenuTabTop;



