import React, { Component, Fragment } from 'react';
import { Table, Button, Icon, Checkbox, Input } from 'antd';
import { setTableCreateNewClass } from 'dump/data';

class TableCreateNewClass extends Component {
  constructor(props) {
    super(props);
    this.state = {};

    this.columns = [
      {
        title: 'Mã học phần',
        dataIndex: 'maHocPhan',
        key: 'maHocPhan',
      },
      {
        title: 'Tên học phần',
        dataIndex: 'tenHocPhan',
        key: 'tenHocPhan',
      },
      {
        title: 'Số TC',
        dataIndex: 'soTC',
        key: 'soTC',
      },
      {
        title: 'Số tiết',
        dataIndex: 'soTiet',
        key: 'soTiet',
      },
      {
        title: 'Hệ số',
        dataIndex: 'heSo',
        key: 'heSo',
      },
      {
        title: 'Số SV',
        dataIndex: 'soSV',
        key: 'soSV',
      },
      {
        title: 'Ký hiệu tên lớp',
        dataIndex: 'ki_hieu_lop_TC',
        render: () =>
          setTableCreateNewClass.length >= 1 ? (
            <Fragment>
              <Input />
            </Fragment>
          ) : null,
      },
      {
        title: 'S.tiết tuần',
        dataIndex: 'so_tiet_tuan',
        render: () =>
          setTableCreateNewClass.length >= 1 ? (
            <Fragment>
              <Input />
            </Fragment>
          ) : null,
      },
      {
        title: 'Số lớp TC',
        dataIndex: 'soLopTC',
        key: 'soLopTC',
      },
      {
        title: 'Số SV tối thiểu',
        dataIndex: 'so_sv_toi_thieu',
        render: () =>
          setTableCreateNewClass.length >= 1 ? (
            <Fragment>
              <Input />
            </Fragment>
          ) : null,
      },
      {
        title: 'Số SV tối đa',
        dataIndex: 'so_sv_toi_da',
        render: () =>
          setTableCreateNewClass.length >= 1 ? (
            <Fragment>
              <Input />
            </Fragment>
          ) : null,
      },
      {
        title: 'Số ca LT/tuần',
        dataIndex: 'soCaLTTuan',
        key: 'soCaLTTuan',
      },
      {
        title: 'Số lớp BT, TH, TN',
        dataIndex: 'soLopBTTHTN',
        key: 'soLopBTTHTN',
      },
      {
        title: 'Số ca TH, BT, TN/tuần',
        dataIndex: 'soCaTHBTTNTuan',
        key: 'soCaTHBTTNTuan',
      },
      {
        title: 'Tự chọn',
        dataIndex: 'tu_chon',
        render: () =>
          setTableCreateNewClass.length >= 1 ? (
            <Fragment>
              <Checkbox />
            </Fragment>
          ) : null,
      },
      {
        title: 'S.tiết LT',
        dataIndex: 'soTietLT',
        key: 'soTietLT',
      },
      {
        title: 'S.tiết TH',
        dataIndex: 'soTietTH',
        key: 'soTietTH',
      },
    ];

    this.rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
      },
      getCheckboxProps: record => ({
        disabled: record.name === 'Disabled User', // Column configuration not to be checked
        name: record.name,
      }),
    };
  }

  render() {
    return (
      <div>
        <Button type="primary" className="btnSetDecisionNumber">
          <Icon type="export" /> Tạo lớp tín chỉ
        </Button>
        <Table
          bordered
          rowSelection={this.rowSelection}
          dataSource={setTableCreateNewClass}
          columns={this.columns}
          scroll={{ x: 'true' }}
        />
      </div>
    );
  }
}

export default TableCreateNewClass;



