import React, { Component } from 'react';
import { Row, Col, Form, Select, DatePicker, InputNumber, Input } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';
import { setMajorsName, setSemesterRegister } from 'dump/data';
import './style.scss';

@Form.create({ name: 'formSerchIndividual' })
class FormInfoCommonCreateNewClass extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    const { Option } = Select;
    return (
      <Form onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} classes={{ isShow: false }} />
          <Col span={8}>
            <Form.Item label="Ngành:">
              {getFieldDecorator('setMajorsName', {})(
                <Select>
                  {setMajorsName.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Kỳ đăng ký:">
              {getFieldDecorator('setSemesterRegister', {})(
                <Select>
                  {setSemesterRegister.map(item => (
                    <Option value={item.code}>{item.name}</Option>
                  ))}
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Từ ngày:">
              <DatePicker className="maxWidth" />
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Đến ngày:">
              <DatePicker className="maxWidth" />
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Từ tuần:">
              <InputNumber min={1} max={50} defaultValue={1} className="maxWidth" />
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Đến tuần:">
              <InputNumber min={1} max={50} defaultValue={1} className="maxWidth" />
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ký hiệu lớp TC:">
              <Input />
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Số tiết tuần:">
              <InputNumber min={1} max={50} defaultValue={1} className="maxWidth" />
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Số SV tối thiểu:">
              <InputNumber min={10} max={50} defaultValue={10} className="maxWidth" />
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Số SV tối đa:">
              <InputNumber min={10} max={150} defaultValue={50} className="maxWidth" />
            </Form.Item>
          </Col>
          <Col span={24} style={{ textAlign: 'right' }}>
            Tổng số SV: <b> 100 </b>
          </Col>
        </Row>
        <br />
      </Form>
    );
  }
}

export default FormInfoCommonCreateNewClass;



